import {
    Link as LinkUiKit,
    Icon,
    List,
    ListItem,
    Navbar,
    NavbarContainer,
    NavbarSticky,
    Offcanvas,
    OffcanvasContainer,
    Section
} from "uikit-react";
import { Outlet, Link } from "react-router-dom";
import Members from "./pages/members";


function Layout(){
    return (
        <>
            <OffcanvasContainer>
                <Section >
                    <NavbarSticky options="animation: uk-animation-slide-top; cls-inactive: uk-navbar-transparent uk-light; top: 556;">
                        <NavbarContainer>
                            <Navbar >
                                <ListItem>
                                    <LinkUiKit toggleOptions="target: #menu;" href="#">
                                        <Icon options="menu" button />
                                    </LinkUiKit>
                                </ListItem>
                            </Navbar>
                        </NavbarContainer>
                    </NavbarSticky>
                    <Offcanvas id="menu" options="overlay: true">
                        <List type="divider">
                            <ListItem>
                                <Link to="/">Home</Link>
                            </ListItem>
                            <ListItem>
                                <Link to="/about">About</Link>
                            </ListItem>
                            <ListItem>
                                <Link to="/members" >Members</Link>
                            </ListItem>
                            <ListItem>
                                <Link to="/contact">Contact Us</Link>
                            </ListItem>
                            <ListItem>
                                <Link to="/ctf">CTF 2023</Link>
                            </ListItem>
                        </List>
                    </Offcanvas>
                </Section>
            </OffcanvasContainer>
            <Outlet />
        </>
    )
}

export default Layout;